
.header{
  display: flex;
  justify-content: space-between;
  border: 3px solid gray;
  border-radius: 14px;
  font-family:Georgia, 'Times New Roman', Times, serif;
}
.logo-container > img{
  width:100px;
  border-radius: 45%;
  padding: 10px;
}
.nav-items > ul{
  display: flex;
  list-style-type: none;
}
.nav-items > ul > li{
  padding:10px;
  margin:10px;
}